@keyframes animStar {
  0% {
    transform: translateY(0px);
  }
  100% {
    transform: translateY(-2000px);
  }
}

.error {
  min-height: 100vh;
  position: relative;
  box-sizing: border-box;
  width: 100%;
  height: 100%;
  text-align: center;
  max-width: 300px;
  margin: 200px auto 0;
}

.number {
  font-weight: 900;
  text-transform: uppercase;
  font-size: 60px;
  color: #1e1e2b;
}

.title {
  font-weight: 900;
  text-transform: uppercase;
  font-size: 60px;
  color: #1e1e2b;
}

.lamp {
  position: absolute;
  left: 0px;
  right: 0px;
  top: 0px;
  margin: 0px auto;
  width: 300px;
  display: flex;
  flex-direction: column;
  align-items: center;
  transform-origin: center top;
  animation-timing-function: cubic-bezier(0.6, 0, 0.38, 1);
  animation: move 5.1s infinite;
}

@keyframes move {
  0% {
    transform: rotate(40deg);
  }
  50% {
    transform: rotate(-40deg);
  }
  100% {
    transform: rotate(40deg);
  }
}

.cable {
  width: 8px;
  height: 248px;
  background-image: linear-gradient(rgb(32 148 218 / 70%), rgb(193 65 25)),
    linear-gradient(rgba(0, 0, 0, 0.7), rgba(0, 0, 0, 0.7)),
    linear-gradient(rgba(0, 0, 0, 0.7), rgba(0, 0, 0, 0.7));
}

.cover {
  width: 200px;
  height: 80px;
  background: #0bd5e8;
  border-top-left-radius: 50%;
  border-top-right-radius: 50%;
  position: relative;
  z-index: 200;
}

.incover {
  width: 100%;
  max-width: 200px;
  height: 20px;
  border-radius: 100%;
  background: #08ffff;
  position: absolute;
  left: 0px;
  right: 0px;
  margin: 0px auto;
  bottom: -9px;
  z-index: 100;
}
.incover .bulb {
  width: 50px;
  height: 50px;
  background-color: #08fffa;
  border-radius: 50%;
  position: absolute;
  left: 0px;
  right: 0px;
  bottom: -20px;
  margin: 0px auto;
  -webkit-box-shadow: 0 0 15px 7px rgba(0, 255, 255, 0.8),
    0 0 40px 25px rgba(0, 255, 255, 0.5),
    -75px 0 30px 15px rgba(0, 255, 255, 0.2);
  box-shadow: 0 0 25px 7px rgb(127 255 255 / 80%),
    0 0 64px 47px rgba(0, 255, 255, 0.5), 0px 0 30px 15px rgba(0, 255, 255, 0.2);
}

.light {
  width: 200px;
  height: 0px;
  border-bottom: 100vh solid rgb(44 255 255 / 24%);
  border-left: 50px solid transparent;
  border-right: 50px solid transparent;
  position: absolute;
  left: 0px;
  right: 0px;
  top: 270px;
  margin: 0px auto;
  z-index: 1;
  border-radius: 90px 90px 0px 0px;
}

.btn {
  background: #5248a0be;
  border: 2px solid #5248a0be;
  border-radius: 18px;
  color: rgba(0, 0, 0, 1);
  display: block;
  margin: 0px auto 0;
  margin-top: 40px;
  height: 50px;
  width: 120px;
}

.btn:hover,
.btn:focus {
  border: 2px solid #5248a0be;
  transform: scale(1.1);
}

.link {
  display: flex;
  height: 40px;
  justify-content: center;
  align-items: center;
  text-decoration: none;
  color: #000;
  font-weight: 700;
}

@media (min-width: 320px) {
  .lamp {
    zoom: 0.5;
  }
}

@media (min-width: 768px) {
  .lamp {
    zoom: 0.8;
  }
  .error {
    max-width: 700px;
    margin: 300px auto 0;
  }
  .number {
    font-size: 80px;
  }

  .title {
    font-size: 80px;
  }
}

@media (min-width: 1280px) {
  .lamp {
    zoom: 0.9;
  }
  .error {
    margin: 350px auto 0;
  }
  .number {
    font-size: 100px;
  }

  .title {
    font-size: 100px;
  }
}
